@media only screen and (max-width: 1440px){
    .video iframe {
        height: calc(170px + (2 - 1.4) * ((100vw - 320px) / (1200 - 800)));
    }
}

@media only screen and (max-width: 1280px) {
    .video {
        margin-top: calc(22px + (2 - 1.4) * ((100vw - 320px) / (1200 - 800)));
    }
}